import React, { Component } from 'react';
import ShareButton from './ShareButton';
import social_icons from '../images/social-icons.jpg';

class ShareButtons extends Component {
  constructor() {
    super();
    this.state = {
      sites: [
        {
          name: 'facebook',
          label: 'Facebook',
          url:
            'https://www.facebook.com/sharer.php?s=100&p[url]=##url##&p[images][0]=##image##&p[title]=##title##&p[summary]=##description##',
          icon: {
            src: null,
            bg: social_icons,
            top: -230,
            left: -284
          }
        },
        {
          name: 'twitter',
          label: 'Twitter',
          url: 'https://twitter.com/intent/tweet?url=##url##&text=##title##',
          icon: {
            src: null,
            bg: social_icons,
            top: -386,
            left: -284
          }
        },
        {
          name: 'linkedin',
          label: 'LinkedIn',
          url: 'https://www.linkedin.com/shareArticle?url=##url##&title=##title##',
          icon: {
            src: null,
            bg: social_icons,
            top: -700,
            left: -284
          }
        },
        {
          name: 'whatsapp',
          label: 'WhatsApp',
          url: 'https://api.whatsapp.com/send?text=##title##',
          icon: {
            src: null,
            bg: social_icons,
            top: -544,
            left: -445
          }
        },
        {
          name: 'googleplus',
          label: 'Google+',
          url: 'https://plus.google.com/share?url=##url##',
          icon: {
            src: null,
            bg: social_icons,
            top: -544,
            left: -284
          }
        },
        {
          name: 'ycombinator',
          label: 'Y Combinator',
          url:
            'https://news.ycombinator.com/submitlink?u=##url##&t=##title##',
          icon: {
            src:
              'data:image/png;base64,',
            bg: '',
            top: -544,
            left: -284
          }
        }
      ]
    };
  }

  generateSocialUrls() {
    const social_networks = this.state.sites;
    
    social_networks.map(site => {
      return site.new_url = this.prepareURL(site.url);
    });

    this.setState(social_networks);
  }

  prepareURL(url) {
    console.log('url in prepareURL is ', this.props.remoteSite.url);
    let template = '##url##';
    url = url.replace(new RegExp(template, 'g'), encodeURIComponent(this.props.remoteSite.url));

    template = '##image##';
    url = url.replace(new RegExp(template, 'g'), encodeURIComponent(this.props.remoteSite.image));

    template = '##title##';
    url = url.replace(new RegExp(template, 'g'), encodeURIComponent(this.props.remoteSite.title));

    template = '##description##';
    url = url.replace(new RegExp(template, 'g'), encodeURIComponent(this.props.remoteSite.description));

    return url;
  }

  componentWillReceiveProps(props) {
    let remoteSite = {
      title: props.title,
      url: props.url,
      description: props.description,
      image: props.image
    };
    console.log('Component is getting new prop', remoteSite);
    this.setState({"remoteSite":remoteSite}, this.generateSocialUrls);
  }

  render() {
    return (
      <div className="container">
        <h2>Click to share</h2>
        <div className="row">
          {this.state.sites.map(site => {
            return (
              <ShareButton
                key={site.name}
                name={site.name}
                label={site.label}
                icon={site.icon}
                url={site.new_url}
              />
            );
          })}
        </div>
      </div>
    );
  }
}

export default ShareButtons;


